<script>
import Modal from "./Modal.vue";
import $ from "jquery";

export default {
  id: "06page",
  name: "06Page",
  components: {
    Modal,
  },
  setup() {
    return {};
  },
  mounted() {
    $("#radioBtn a").on("click", function () {
      //console.log($(this).data("title"));
      var sel = $(this).data("title");
      var tog = $(this).data("toggle");
      $("#" + tog).prop("value", sel);

      $('a[data-toggle="' + tog + '"]')
        .not('[data-title="' + sel + '"]')
        .removeClass("active")
        .addClass("notActive");
      $('a[data-toggle="' + tog + '"][data-title="' + sel + '"]')
        .removeClass("notActive")
        .addClass("active");
    });
  },
  methods: {
    changeVersion(v) {
      if (v == "TW") {
        this.titleText = "每月任務敵人關卡查找";
      } else if (v == "US") {
        this.titleText = "Monthly Missions Finder";
      } else if (v == "JP") {
        this.titleText = "マンスリーミッションのステージ検索";
      }
    },
    startSearch() {
      var val = document.getElementById("searchEnemyInput").value;
      if (val == "") {
        console.log("no input");
      } else {
        //console.log(val);
        this.dofilter(val, false);
      }
    },
    resetMemory() {
      console.log(this.alreadySelectEnemyList);
      for (var i = 0; i < this.alreadySelectEnemyList.length; i++) {
        var element = document.getElementById(
          "select_".concat(this.alreadySelectEnemyList[i])
        );
        element.classList.toggle("unSelect");
      }

      var keys = Object.keys(this.firstWordList);
      for (var j = 0; j < keys.length; j++) {
        var id = this.firstWordList[keys[j]];
        if (!document.getElementById(id).classList.contains("unSelectFliter"))
          document.getElementById(id).classList.toggle("unSelectFliter");
      }

      this.alreadySelectEnemyList = [];
      this.ansStage = {};
      this.firstWordList = {};
    },
    changeTitle(value) {
      this.enemyName = value.jpName;
      this.imageSrc = value.imgSrc;
      this.productModal.show();
    },
    changeStage(key, value) {
      this.resetMemory();

      this.stageName = value.ct;
      this.stageEnemyList = {};
      this.filterStageEnemyList = {};
      // 取得篇章所有敵人
      var keys = Object.keys(this.stageDetailList[key]);
      for (var i = 0; i < keys.length; i++) {
        var enemyInfo = this.stageDetailList[key][keys[i]]["info"];
        for (var j = 0; j < enemyInfo.length; j++) {
          var tmpId = enemyInfo[j]["id"];
          this.stageEnemyList[tmpId] = {};

          this.stageEnemyList[tmpId]["selectId"] = "select_".concat(tmpId);
          this.stageEnemyList[tmpId]["ctName"] =
            this.enemyList["id_".concat(tmpId)]["ctName"];
          this.stageEnemyList[tmpId]["jpName"] =
            this.enemyList["id_".concat(tmpId)]["jpName"];
          this.stageEnemyList[tmpId]["enName"] =
            this.enemyList["id_".concat(tmpId)]["enName"];
          this.stageEnemyList[tmpId]["imgSrc"] =
            this.enemyList["id_".concat(tmpId)]["imgSrc"];
        }
      }
      keys = Object.keys(this.stageEnemyList);
      // var firstWordList = {};
      for (i = 0; i < keys.length; i++) {
        this.firstWordList[this.stageEnemyList[keys[i]]["jpName"][0]] =
          this.stageEnemyList[keys[i]]["jpName"][0];
        //console.log(this.stageEnemyList[keys[i]]["jpName"][0]);
        this.filterStageEnemyList = this.stageEnemyList;
      }
      console.log(this.filterStageEnemyList);

      var firstWordKeys = Object.keys(this.firstWordList);
      firstWordKeys.sort();
      var tmpFirstWordMap = {};
      for (i = 0; i < firstWordKeys.length; i++) {
        tmpFirstWordMap[firstWordKeys[i]] = firstWordKeys[i];
      }
      this.firstWordList = tmpFirstWordMap;
    },
    selectEnemy(id) {
      var element = document.getElementById("select_".concat(id));

      // 處理選擇/取消選擇
      if (
        this.alreadySelectEnemyList.length < 3 &&
        element.classList.contains("unSelect")
      ) {
        element.classList.toggle("unSelect");
        this.alreadySelectEnemyList.push(id);
      } else if (!element.classList.contains("unSelect")) {
        element.classList.toggle("unSelect");
        const index = this.alreadySelectEnemyList.indexOf(id);
        this.alreadySelectEnemyList.splice(index, 1);
      }

      console.log("select: " + this.alreadySelectEnemyList.length);

      // find enemies in stage
      var keys = Object.keys(this.stageList);
      var stageKey = "";
      for (var i = 0; i < keys.length; i++) {
        if (this.stageList[keys[i]].ct == this.stageName) {
          stageKey = keys[i];
          break;
        }
      }

      keys = Object.keys(this.stageDetailList[stageKey]);
      this.ansStage = {};
      for (i = 0; i < keys.length; i++) {
        var stageEnemyList = this.stageDetailList[stageKey][keys[i]]["info"];
        for (var j = 0; j < stageEnemyList.length; j++) {
          for (var k = 0; k < this.alreadySelectEnemyList.length; k++) {
            if (stageEnemyList[j].id == this.alreadySelectEnemyList[k]) {
              //this.ansStage[keys[j]] = {};
              var enemyInfo = this.stageDetailList[stageKey][keys[i]]["info"];

              var tmpStageEnemyInfo = {};

              for (var l = 0; l < enemyInfo.length; l++) {
                // tmpStageEnemyInfo.push(
                //   this.enemyList["id_".concat(enemyInfo[l]["id"])]
                // );
                tmpStageEnemyInfo[enemyInfo[l]["id"]] =
                  this.enemyList["id_".concat(enemyInfo[l]["id"])];
                tmpStageEnemyInfo[enemyInfo[l]["id"]]["select"] = false;
                if (this.alreadySelectEnemyList.includes(enemyInfo[l]["id"]))
                  tmpStageEnemyInfo[enemyInfo[l]["id"]]["select"] = true;
              }
              //console.log(tmpStageEnemyInfo);
              this.ansStage[keys[i]] = tmpStageEnemyInfo;
            }
          }
        }
      }

      var tmpAnsStage = this.ansStage;
      var tmpAlreadySelectEnemyList = this.alreadySelectEnemyList;

      setTimeout(function () {
        var stageKeys = Object.keys(tmpAnsStage);
        for (var idx = 0; idx < stageKeys.length; idx++) {
          var stageEnemyKeys = Object.keys(tmpAnsStage[stageKeys[idx]]);
          console.log(stageKeys);

          for (var idx2 = 0; idx2 < stageEnemyKeys.length; idx2++) {
            // console.log(stageKeys[idx] + "_" + stageEnemyKeys[idx2]);
            var stageEnemyUnSelectId =
              stageKeys[idx] + "_" + stageEnemyKeys[idx2];
            console.log(stageEnemySelectId);
            var addDom = document.getElementById(stageEnemyUnSelectId);
            if (addDom != null) addDom.classList.add("unSelect");
          }
          for (var idx3 = 0; idx3 < tmpAlreadySelectEnemyList.length; idx3++) {
            var stageEnemySelectId =
              stageKeys[idx] + "_" + tmpAlreadySelectEnemyList[idx3];
            //console.log(stageEnemySelectId);
            var removeDom = document.getElementById(stageEnemySelectId);

            if (removeDom != null) removeDom.classList.remove("unSelect");
          }
        }
      }, 100);

      // for (var idx = 0; idx < stageKeys.length; idx++)
      //   console.log("ppp: " + stageKeys[idx]);

      console.log(this.ansStage);
    },
    dofilter(k, isQuickSearch) {
      //console.log(k.length);
      var temAlready = this.alreadySelectEnemyList;
      var keys = Object.keys(this.firstWordList);
      if (k.length == 1 && isQuickSearch) {
        // 控制日文快選
        var preFliterId = "";
        for (var j = 0; j < keys.length; j++) {
          var id = this.firstWordList[keys[j]];
          if (
            !document.getElementById(id).classList.contains("unSelectFliter")
          ) {
            document.getElementById(id).classList.toggle("unSelectFliter");
            preFliterId = id;
          }
        }

        this.filterStageEnemyList = this.stageEnemyList;
        if (preFliterId == k[0]) {
          setTimeout(function () {
            for (var ii = 0; ii < temAlready.length; ii++) {
              var idd = temAlready[ii];
              var element = document.getElementById("select_".concat(idd));
              //處理選擇 / 取消選擇;
              if (element != null) element.classList.remove("unSelect");
              var element2 = document.getElementById(idd);
              //處理選擇 / 取消選擇;
              if (element2 != null) element2.classList.remove("unSelect");
            }
          }, 10);
          return;
        }

        var dom = document.getElementById(k[0]);
        if (dom != null) dom.classList.toggle("unSelectFliter");

        keys = Object.keys(this.filterStageEnemyList);
        var tmpAns = {};
        for (var i = 0; i < keys.length; i++) {
          if (k == this.filterStageEnemyList[keys[i]]["jpName"][0])
            tmpAns[keys[i]] = this.filterStageEnemyList[keys[i]];
        }
        this.filterStageEnemyList = tmpAns;
        return;
      } else {
        for (j = 0; j < keys.length; j++) {
          id = this.firstWordList[keys[j]];
          document.getElementById(id).classList.add("unSelectFliter");
        }
      }

      keys = Object.keys(this.filterStageEnemyList);
      tmpAns = {};
      for (i = 0; i < keys.length; i++) {
        if (
          this.filterStageEnemyList[keys[i]]["ctName"].includes(k) ||
          this.filterStageEnemyList[keys[i]]["jpName"].includes(k) ||
          this.filterStageEnemyList[keys[i]]["enName"].includes(k)
        )
          tmpAns[keys[i]] = this.filterStageEnemyList[keys[i]];
      }

      //console.log(tmpAns);
      this.filterStageEnemyList = tmpAns;

      setTimeout(function () {
        for (var ii = 0; ii < temAlready.length; ii++) {
          var idd = temAlready[ii];
          var element = document.getElementById("select_".concat(idd));
          //處理選擇 / 取消選擇;
          if (element != null) element.classList.remove("unSelect");
          var element2 = document.getElementById(idd);
          //處理選擇 / 取消選擇;
          if (element2 != null) element2.classList.remove("unSelect");
        }
      }, 10);

      //console.log(this.filterStageEnemyList);
    },
    refreshPage() {
      console.log("refresh");
      console.log(this.alreadySelectEnemyList);
      for (var ii = 0; ii < this.alreadySelectEnemyList.length; ii++) {
        var idd = this.alreadySelectEnemyList[ii];
        var element = document.getElementById("select_".concat(idd));
        console.log("idd: " + "select_".concat(idd));
        console.log(element);
        // 處理選擇/取消選擇
        if (element != null) element.classList.remove("unSelect");
      }
    },
  },
